.signup {
  height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 70px;
}

.signup__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  padding: 25px;
  padding-top: 15px;
}

.signup__logo > a > img {
  object-fit: contain;
  width: 90px;
}

.signup__language {
  display: flex;
  gap: 5px;
  align-items: flex-end;
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 50px;
  font-size: 15px;
  transition: all 0.2s;
}

.signup__language:hover {
  background-color: hsla(0, 0%, 50.2%, 0.125);
}

.signup__language > span {
  font-weight: 500;
}

.signup__info {
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-width: 330px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 80px;
}

.signup__info > h1 {
  font-weight: 500;
  font-size: 40px;
  margin-left: -60px;
}

.signup__form {
  display: flex;
  flex-direction: column;
}

.signup__form > label {
  color: #5c5e62;
  font-weight: 500;
  font-size: 15px;
  padding-left: 20px;
}

.signup__form > input {
  margin-bottom: 30px;
  background-color: #f4f4f4;
  border: 1px solid #f4f4f4;
  outline: none;
  border-radius: 50px;
  padding: 12px 20px;
  color: #393c41;
  font-weight: 600;
}

.signup__form > input:focus {
  border: 1px solid #d6d6d7;
  transition: all 0.2s;
}

.signup__error {
  color: #ff3333;
  margin-bottom: 1rem;
}